import http from '../http';
const sign = {
    /**
     * 获取用户抽奖次数和中奖列表
     * @param {*}
     */
    getInfoList(id) {
        // return http.get(`js/v7/Prize/GetUserSignPrize`,{headers:{sign:`${id}`}});
        return http.get(`js/v7/Prize/GetUserSignPrize`);
    },
    /**
     * 抽奖
     * @param {*} type
     */
    getDraw(type) {
        return http.post(`js/v7/Prize/Lottery`,{type:`${type}`});
    },
    /**
     * 填写中奖信息(实物奖品)
     * @param {*} model
     */
    getFill(model) {
        return http.post(`js/v7/Prize/FillInInformation`,model);
    }

}
export default sign;


