'use strict';
import http from '../http';

const order = {
    getOrders(filters) {//获取购物车
        return http.get(`js/v7/EcOrder/MyOrder`,{ params: filters });
    },
    getOrderDetail(filters){
        return http.get(`js/v7/ecorder/Detail`,{ params: filters });        
    },
    creatComment(modle){
        return http.post(`/js/v7/appraise/create`,modle);
    },
    viewComment(id){
        return http.get(`/js/v7/appraise/GetAppraise?orderId=${id}`);
    },
    deleOrder(id){
        return http.post(`/js/v7/ecorder/Delete`,{paymentId:`${id}`});
    },
    OrderNewList(filters){
        return http.get(`/js/v7/EcOrder/MyOrderForNew`,{ params: filters });
    },
    OrderNewDetail(filters){
        return http.get(`/js/v7/ecorder/DetailForNew`,{ params: filters });
    },
    OrderserviceCard(model){
        return http.get(`/js/v7/EcOrder/GetSecurityCard`,{ params: model });
    },
    detailCrmForNew(filters){
        return http.get(`/js/v7/EcOrder/DetailCrmForNew`,{ params: filters });
    },
    hideOrder(modle){
        return http.post(`/js/v7/ecorder/CrmOrderDelete`,modle)
    }
    
};
export default order;


