import http from '../http';

const service = {
    list(filters){
        return http.get(`/js/v7/aftersale/center`,{ params: filters });
    },
    creatRights(model) {//申请维权
        return http.post(`/js/v7/aftersale/CreateSafeguardingRights`, model);
    },
    createComplain(model) { //投诉
        return http.post(`/js/v7/aftersale/CreateComplain`, model);
    },
    creatSub(model) {//建议
        return http.post(`/js/v7/aftersale/CreateSuggest`, model);
    }
};
export default service;


