'use strict';
import http from '../http';

const custom = {
    getAdviser() {
        return http.get(`/Js/v7/UserCustomization/GetAdviserList`);
    },
    getTarget(filters) {
        return http.get(`Js/v7/UserCustomization/GetTemplate`);
    },
    getProject(id) {
        return http.get(`Js/v7/UserCustomization/GetProjectByTemplateId?id=${id}`);
    },
    getArea() {
        return http.get(`Js/v7/UserCustomization/GetData`);
    },
    confirm(model){
        return http.post(`Js/v7/UserCustomization/Save`, model);
    },
    'getOne_project'(){
        return http.get(`Js/v7/UserCustomization/GetLevelOneProject`);
    },
    'getTwo_project'(id){
        return http.get(`Js/v7/UserCustomization/GetProjectById?id=${id}`);
    },
    getUserCustomzation(id) {
        return http.get(`Js/v7/UserCustomization/GetUserCustomzationById?id=${id}`);
    },
    getSuccess(id){
        return http.get(`Js/v7/UserCustomization/GetProductByUserCustomId?id=${id}`);
    }

};
export default custom;


