'use strict';
import http from '../http';

const themes = {
    get(id) {
        return http.get(`/api/v1/themes/${id}`);
    },
    update: (id, model) => {
        return http.put(`/api/v1/themes/${id}`, model);
    },
    list: (filters) => {
        return http.get(`/api/v1/themes/list`, { params: filters })
    },
};

export default themes;


