<template>
    <section class="m-center">
        <div class="cover" v-if="pageStatus == 0" v-cloak>
            <div class="loadinging">
                <img src="../images/loading.gif">
            </div>
        </div>
        <div class="cover" v-if="pageStatus == 1" v-cloak>
            <div class="noData">
                <slot name="empty">
                    <img src="../images/no-data.png">
                </slot>
            </div>
        </div>
        <div class="cover" v-if="!onLine" v-cloak>
            <div class="noData">
                <img src="../images/no-net.png">
            </div>
        </div>
        <slot></slot>
    </section>
</template>
<script>
import { mapState } from 'vuex';
export default {
    props: {
        pageStatus: {
            type: Number,
            default: 0
        }
    },
    computed: {
        ...mapState(['onLine'])
    }
}
</script>


