// example
import list from '../views/list';

// import index from '../views/index';

//subject
// import subject from '../views/subject/index';

const index = q => require.ensure([], () => q(require('../views/index')), 'bundle');
//subject
const subject = q => require.ensure([], () => q(require('../views/subject/index')), 'bundle');

//activity
const activityIndex = q => require.ensure([], () => q(require('../views/activity/index')), 'bundle');
const activityList = q => require.ensure([], () => q(require('../views/activity/list')), 'bundle');
const activityDetail = q => require.ensure([], () => q(require('../views/activity/detail')), 'bundle');
const activitySign = q => require.ensure([], () => q(require('../views/activity/sign')), 'bundle');
const activityComplaint = q => require.ensure([], () => q(require('../views/activity/complaint')), 'bundle');


/* 
 ***************电商项目页面***************
 */
// product
const tagsHtm = q => require.ensure([], () => q(require('../views/prod/tagshtm')), 'bundle');
const prodDetails = q => require.ensure([], () => q(require('../views/prod/details')), 'bundle');
const prodCarts = q => require.ensure([], () => q(require('../views/prod/carts')), 'bundle');
// const prodCoupon = q => require.ensure([], () => q(require('../views/prod/coupon')), 'bundle');
const prodNotice = q => require.ensure([], () => q(require('../views/prod/notice')), 'bundle');
// order
const orderMaster = q => require.ensure([], () => q(require('../views/orders/master')), 'bundle');
const orderAll = q => require.ensure([], () => q(require('../views/orders/allOrder')), 'bundle');
const orderWaitPay = q => require.ensure([], () => q(require('../views/orders/waitPayOrder')), 'bundle');
const orderWaitCharge = q => require.ensure([], () => q(require('../views/orders/waitChargeOrder')), 'bundle');
const orderAlreadyConsumed = q => require.ensure([], () => q(require('../views/orders/alreadyConsumedOrder')), 'bundle');
const orderRefund = q => require.ensure([], () => q(require('../views/orders/refundOrder')), 'bundle');
const orderCreate = q => require.ensure([], () => q(require('../views/orders/createByCart')), 'bundle');
const orderDetails = q => require.ensure([], () => q(require('../views/orders/details')), 'bundle');
const orderPay = q => require.ensure([], () => q(require('../views/orders/pay')), 'bundle');
const paySuccess = q => require.ensure([], () => q(require('../views/orders/success')), 'bundle');
const payfail = q => require.ensure([], () => q(require('../views/orders/failure')), 'bundle');
const orderComment = q => require.ensure([], () => q(require('../views/orders/comment')), 'bundle');
const alredyComment = q => require.ensure([], () => q(require('../views/orders/alredyComment')), 'bundle');
//refund
const refund = q => require.ensure([], () => q(require('../views/refund/index')), 'bundle');



/**========
    搜索
=========*/
const search = q => require.ensure([], () => q(require('../views/search/index')), 'bundle');
const searchMaster = q => require.ensure([], () => q(require('../views/search/master')), 'bundle');
const searchAll = q => require.ensure([], () => q(require('../views/search/all')), 'bundle');
const searcDiary = q => require.ensure([], () => q(require('../views/search/diary')), 'bundle');
const searcDoctor = q => require.ensure([], () => q(require('../views/search/doctor')), 'bundle');
const searcHospital = q => require.ensure([], () => q(require('../views/search/hospital')), 'bundle');
const searcKnow = q => require.ensure([], () => q(require('../views/search/know')), 'bundle');
const searcPord = q => require.ensure([], () => q(require('../views/search/prod')), 'bundle');
const searcTag = q => require.ensure([], () => q(require('../views/search/tag')), 'bundle');
const searcUser = q => require.ensure([], () => q(require('../views/search/user')), 'bundle');


/**========
    定制
=========*/
const schemeIndex = q => require.ensure([], () => q(require('../views/custom/index')), 'bundle');
const schemeGroup = q => require.ensure([], () => q(require('../views/custom/group')), 'bundle');
const schemeItem = q => require.ensure([], () => q(require('../views/custom/item')), 'bundle');
const schemeItemAll = q => require.ensure([], () => q(require('../views/custom/item_all')), 'bundle');
const schemeInfo = q => require.ensure([], () => q(require('../views/custom/info')), 'bundle');
const schemeTel = q => require.ensure([], () => q(require('../views/custom/tel')), 'bundle');
const schemeAdviser = q => require.ensure([], () => q(require('../views/custom/adviser')), 'bundle');
const schemeDetails = q => require.ensure([], () => q(require('../views/custom/details')), 'bundle');
const schemeSuccess = q => require.ensure([], () => q(require('../views/custom/success')), 'bundle');

/**========
    签到
=========*/
const sign = q => require.ensure([], () => q(require('../views/sign/index')), 'bundle');

/**========
    商品聚合
=========*/
const wikiDetail = q => require.ensure([], () => q(require('../views/wiki/detail')), 'bundle');

/**========
    积分规则、用户协议
=========*/
const pointRule = q => require.ensure([], () => q(require('../views/points/rules')), 'bundle');
const agreement = q => require.ensure([], () => q(require('../views/points/agreement')), 'bundle');

/**========
    广告详情
=========*/
const advDetail = q => require.ensure([], () => q(require('../views/advertisement/detail')), 'bundle');

/**========
    分期
=========*/
const stages = q => require.ensure([], () => q(require('../views/instalment/index')), 'bundle');
const stagesDesc = q => require.ensure([], () => q(require('../views/instalment/introduce')), 'bundle');

/**========
   医院详情
=========*/
const hosptailDetail = q => require.ensure([], () => q(require('../views/hospital/detail')), 'bundle');

/**========
   医生详情
=========*/
const doctorDetail = q => require.ensure([], () => q(require('../views/doctor/details')), 'bundle');
const doctorProd = q => require.ensure([], () => q(require('../views/doctor/doctorProd')), 'bundle');
const doctorDiary = q => require.ensure([], () => q(require('../views/doctor/diary')), 'bundle');

/**========
   售后服务
=========*/
const service = q => require.ensure([], () => q(require('../views/service/card')), 'bundle');//保障卡
const serviceList = q => require.ensure([], () => q(require('../views/service/list')), 'bundle');//客服服务列表
const rights = q => require.ensure([], () => q(require('../views/service/rights')), 'bundle');//订单维权
const sug = q => require.ensure([], () => q(require('../views/service/sug')), 'bundle');//建议
const complain = q => require.ensure([], () => q(require('../views/service/complain')), 'bundle');//投诉
/**========
  转诊订单详情
=========*/
const orderForCrm = q => require.ensure([], () => q(require('../views/orders/detailForCrm')), 'bundle');

/**========
  转诊订单详情
=========*/
const handBook = q => require.ensure([], () => q(require('../views/handbook/list')), 'bundle');

/**
 * 邀请页面
 */
const rewardIndex = q => require.ensure([], () => q(require('../views/reward/index')), 'bundle');
const rewardInvite = q => require.ensure([], () => q(require('../views/reward/invite')), 'bundle');
const rewardRecord = q => require.ensure([], () => q(require('../views/reward/record')), 'bundle');
const rewardRule = q => require.ensure([], () => q(require('../views/reward/rule')), 'bundle');
const rewardAbout = q => require.ensure([], () => q(require('../views/reward/about')), 'bundle');

const routes = [

    {
        name: 'home',
        path: '/',
        component: index
    },

    {
        name: 'list',
        path: '/list',
        component: list
    },


    {
        name: 'activity',
        path: '/activity',
        component: activityIndex
    },
    {
        name: 'activity.list',
        path: '/activity/list',
        component: activityList
    },
    {
        name: 'activity.detail',
        path: '/activity/detail',
        component: activityDetail
    },
    {
        name: 'activity.sign',
        path: '/activity/sign',
        component: activitySign
    },
    {
        name: 'activity.complaint',
        path: '/activity/complaint',
        component: activityComplaint
    },
    {
        name: 'subject',
        path: '/subject',
        component: subject
    },
    {
        name: 'prod.details',
        path: '/prod/details',
        component: prodDetails

    },
    {
        name: 'prod.carts',
        path: '/prod/carts',
        component: prodCarts
    },
    {
        name: 'prod.tags',
        path: '/prod/tags',
        component: tagsHtm
    },
    {
        name: 'orders.createByCart',
        path: '/orders/createByCart',
        component: orderCreate
    },

    {
        path: '/orders',
        component: orderMaster,
        children: [
            { path: '', redirect: { name: 'orders' } },
            {
                name: 'orders',
                path: 'all',
                component: orderAll,
            },
            {
                name: 'orders.waitPay',
                path: 'waitPay',
                component: orderWaitPay,
            },
            {
                name: 'orders.waitCharge',
                path: 'waitCharge',
                component: orderWaitCharge,
            },
            {
                name: 'orders.alreadyConsumed',
                path: 'alreadyConsumed',
                component: orderAlreadyConsumed,
            },
            {
                name: 'orders.refundOrder',
                path: 'refundOrder',
                component: orderRefund,
            },
        ]
    },

    {
        name: 'search',
        path: '/search',
        component: searchMaster,
        children: [
            { path: '', redirect: { name: 'search.all' } },
            {
                name: 'search.all',
                path: 'all',
                component: searchAll,
            },
            {
                name: 'search.diary',
                path: 'diary',
                component: searcDiary,
            },
            {
                name: 'search.doctor',
                path: 'doctor',
                component: searcDoctor,
            },
            {
                name: 'search.hospital',
                path: 'hospital',
                component: searcHospital,
            },
            {
                name: 'search.know',
                path: 'know',
                component: searcKnow,
            },
            {
                name: 'search.prod',
                path: 'prod',
                component: searcPord,
            },
            {
                name: 'search.tag',
                path: 'tag',
                component: searcTag,
            },
            {
                name: 'search.user',
                path: 'user',
                component: searcUser,
            },
        ]

    },
    {
        name: 'orders.details',
        path: '/orders/details',
        component: orderDetails
    },
    {
        name: 'orders.orderForCrm',
        path: '/orders/orderForCrm',
        component: orderForCrm
    },

    {
        name: 'orders.pay',
        path: '/orders/pay',
        component: orderPay
    },
    {
        name: 'orders.paySuccess',
        path: '/orders/paySuccess',
        component: paySuccess
    },
    {
        name: 'orders.payfail',
        path: '/orders/payfail',
        component: payfail
    },
    {
        name: 'orders.comment',
        path: '/orders/comment',
        component: orderComment
    },
    {
        name: 'orders.alredyComment',
        path: '/orders/alredyComment',
        component: alredyComment
    },
    {
        name: 'refund',
        path: '/refund',
        component: refund
    },
    {
        name: 'custom.res',
        path: '/custom/',
        component: schemeIndex,
        children: [
            { path: '', redirect: { name: 'custom.adviser' } },
            {
                name: 'custom.adviser',
                path: 'adviser',
                component: schemeAdviser,
            },
            {
                name: 'custom.group',
                path: 'group',
                component: schemeGroup,
            },
            {
                name: 'custom.item',
                path: 'item',
                component: schemeItem,
            },
            {
                name: 'custom.itemAll',
                path: 'itemAll',
                component: schemeItemAll,
            },
            {
                name: 'custom.info',
                path: 'info',
                component: schemeInfo,
            },
            {
                name: 'custom.tel',
                path: 'tel',
                component: schemeTel,
            },
            {
                name: 'custom.detail',
                path: 'detail',
                component: schemeDetails,
            },
            {
                name: 'custom.success',
                path: 'success',
                component: schemeSuccess
            }
        ]

    },
    // {
    //     name: 'prod.coupon',
    //     path: '/prod/coupon',
    //     component: prodCoupon
    // },
    {
        name: 'prod.notice',
        path: '/prod/notice',
        component: prodNotice
    },
    {
        name: 'sign',
        path: '/sign',
        component: sign
    },
    {
        name: 'wiki.detail',
        path: '/wiki/detail',
        component: wikiDetail
    },
    {
        name: 'points.rules',
        path: '/points/rules',
        component: pointRule
    },
    {
        name: 'user.agreement',
        path: '/user/agreement',
        component: agreement
    },
    {
        name: 'advertisement.detail',
        path: '/advertisement/detail',
        component: advDetail
    },
    {
        name: 'instalment',
        path: '/instalment',
        component: stages
    },
    {
        name: 'instalment.introduce',
        path: '/instalment/introduce',
        component: stagesDesc
    },
    {
        name: 'hospital.detail',
        path: '/hospital/detail',
        component: hosptailDetail
    },
    {
        name: 'doctor.detail',
        path: '/doctor/detail',
        component: doctorDetail
    },
    {
        name: 'doctor.prod',
        path: '/doctor/prod',
        component: doctorProd
    },
    {
        name: 'doctor.diary',
        path: '/doctor/diary',
        component: doctorDiary
    },
    {
        name: 'service',
        path: '/service',
        component: service
    },
    {
        name: 'service.list',
        path: '/service/list',
        component: serviceList
    },
    {
        name: 'service.rights',
        path: '/service/rights',
        component: rights
    },
    {
        name: 'service.sug',
        path: '/service/sug',
        component: sug
    },
    {
        name: 'service.complain',
        path: '/service/complain',
        component: complain
    },
    {
        name: 'handBook',
        path: '/handBook',
        component: handBook
    },
    {
        name: 'reward.index',
        path: '/reward/index',
        component: rewardIndex
    },
    {
        name: 'reward.invite',
        path: '/reward/invite',
        component: rewardInvite
    },
    {
        name: 'reward.record',
        path: '/reward/record',
        component: rewardRecord
    },
    {
        name: 'reward.rule',
        path: '/reward/rule',
        component: rewardRule
    },
    {
        name: 'reward.about',
        path: '/reward/about',
        component: rewardAbout
    }
];
export default routes;


