<template>
    <section>
        <div class="city-container" v-if="apiCityData" :style="{height:hNum+'px'}">
            <div class="select-left" ref="selectLeft">
                <div class="province-item" v-if="hotCitys[0]">
                    <div class="item-title">GPS位置</div>
                    <div class="item-inner flex-between">
                        <div @click="onCity(hotCitys[0].Name)">
                            {{hotCitys[0].Name || ''}}
                        </div>
                        <div class="refresh" @click="refresh">
                            <img src="../../images/rslocation.png" alt="" :class="refreshIng?'rotateDom':''">
                            <span>刷新定位</span>
                        </div>
                    </div>
                </div>
                <div class="province-item">
                    <div class="item-title hot-title">国内医美热门城市</div>
                    <div class="item-inner" v-if="hotCitys.length">
                        <div class="hot-tag-container">
                            <div class="hots-tag" v-for="(hCity,hIndex) in hotCitys" :key="hIndex" @click="onCity(hCity.Name)">{{hCity.Name}}</div>
                        </div>
                    </div>
                </div>
                <div class="province-item">
                    <div class="item-title all-title">*</div>
                    <div class="item-inner" @click="onCity('')">
                        全国
                    </div>
                </div>
                <div class="province-item" v-for="(item,index) in Object.keys(renderData).sort()" :key="index">
                    <div class="item-title py-title">{{item.toUpperCase()}}</div>
                    <div class="item-inner" v-for="(province,pIndex) in renderData[item]" :key="pIndex" @click="tapProvince(province)" :class="provinceName == province.Name ? 'active' : ''">
                        {{province.Name}}
                    </div>
                </div>
            </div>
            <div class="select-right">
                <div v-for="(nav,nIndex) in navDatas" :key="nIndex" @click="tapFirst(nav)" :class="navName == nav ? 'active' : '' ">
                    {{nav}}
                </div>
            </div>
            <div class="child-container animated" :class="childAnimate">
                <div class="child-pop" @click="closeChild"></div>
                <div class="child-inner" v-if="childList">
                    <div class="parent-name">{{provinceName}}</div>
                    <div class="inner-border" v-if="provinceName"></div>
                    <div class="child-item" v-for="(item,index) in childList" :key="index" @click="tapCitys(item.Name,item.Id)">
                        {{item.Name}}
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>
<style lang="less" scoped>
@import "../../less/font.less";
.inner-border{
    width: 100%;
    height: .2rem;
    background: #f5f5f5;
}
.refresh {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0.5rem;
  background: #f5f5f5;
  width: 1.8rem;
  border-radius: 5px;
  font-size: @font-24;
  color: #333;
  img {
    width: 0.3rem;
    margin-right: 0.1rem;
  }
}
.animated {
  -webkit-animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@-moz-keyframes rotate {
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}

.rotateDom {
  display: block;
  -webkit-transition-property: -webkit-transform;
  -webkit-transition-duration: 1s;
  -moz-transition-property: -moz-transform;
  -moz-transition-duration: 1s;
  -o-transition-property: -o-transform;
  -o-transition-duration: 1s;
  -ms-transition-property: -ms-transform;
  -ms-transition-duration: 1s;
  -webkit-animation: rotate 1s linear infinite;
  -moz-animation: rotate 1s linear infinite;
  -o-animation: rotate 1s linear infinite;
  -ms-animation: rotate 1s linear infinite;
  animation: rotate 1s linear infinite;
}
@-o-keyframes rotate {
  from {
    -o-transform: rotate(0deg);
  }
  to {
    -o-transform: rotate(360deg);
  }
}
@-webkit-keyframes rightIn {
  from {
    right: -100vw;
  }
  to {
    right: 0;
  }
}
.childRightIn {
  -webkit-animation-name: rightIn;
}
@-webkit-keyframes rightOut {
  from {
    right: 0;
  }
  to {
    right: -100vw;
  }
}
.childRightOut {
  -webkit-animation-name: rightOut;
}
.hot-tag-container {
  display: flex;
  flex-wrap: wrap;
}
.city-container {
  width: 100vw;
  height: 100%;
  //   overflow-y: auto;
  //   -webkit-overflow-scrolling: touch;
  overflow-x: hidden;
  display: flex;
  position: relative;
}
.select-left,
.select-right {
  width: 6.9rem;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.select-right {
  padding: 0.3rem 0;
  width: 0.6rem;
  background: #f8f8f8;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: @font-24;
  color: #999;
  div {
    line-height: 0.65rem;
  }
}
.item-title {
  background: #f8f8f8;
  padding: 0.3rem;
  font-size: @font-26;
  color: #999;
  font-weight: bold;
}
.item-inner {
  background: #fff;
  padding: 0.3rem;
  font-size: @font-28;
  color: #333;
  border-bottom: 0.01rem solid #e1e1e1;
}
.item-inner:last-child {
  border: none;
}
.hots-tag {
  width: 1.82rem;
  height: 0.8rem;
  background: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hots-tag:nth-child(2),
.hots-tag:nth-child(5) {
  margin: 0 0.4rem;
}
.hots-tag:nth-child(4),
.hots-tag:nth-child(5),
.hots-tag:nth-child(6) {
  margin-top: 0.25rem;
}
.active {
  color: #ff8686;
}
.child-container {
  position: absolute;
  z-index: 1000;
  width: 100vw;
  height: 100%;
  right: -100vw;
  .child-pop {
    width: 100vw;
    height: 100%;
  }
  .child-inner {
    width: 5.5rem;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background: #fff;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    box-shadow: 0px 0px 18px #999;
    .parent-name {
      font-size: @font-30;
      color: #333;
      padding: 0.3rem;
    }
    .child-item {
      font-size: @font-30;
      color: #333;
      padding: 0.3rem;
      border-bottom: 0.01rem solid #e1e1e1;
    }
    .child-item:last-child {
      border: none;
    }
  }
}
</style>
<script>
import api from 'plus/api';
import { setTimeout } from 'timers';
import Toast from 'components/toast';
export default {
    data() {
        return {
            renderData: [],
            navDatas: ['GPS', '热门', '全国'],
            hotCitys: [],
            navName: 'GPS',
            provinceName: '',
            childAnimate: '',
            childList: void 0,
            refreshIng: false,
        }
    },
    props: {
        apiCityData: void 0,
        hNum: void 0,
    },
    mounted() {
        this.pinyinSort(this.apiCityData.Data);
        this.hotCitys = this.apiCityData.HotCity;
        if (this.isIphone) {
            this.$refs.selectLeft.onscroll = () => {
                if (event.target.className != 'select-left') return;
                this.childAnimate = 'childRightOut';
            }
        }
    },
    methods: {
        pinyinSort(arr) {
            let data = {};
            for (var i of arr) {
                let p = i.SpellName.substring(0, 1);
                if (!data[p]) data[p] = [];
                data[p].push(i);
            }
            for (var k of Object.keys(data).sort()) {
                this.navDatas.push(k.toUpperCase());
            }
            this.renderData = data;
        },
        tapFirst(name) {
            event.preventDefault();
            event.stopPropagation();
            this.navName = name;
            let title = document.querySelectorAll('.py-title');
            for (let i of title) {
                if (i.innerText == name) {
                    return this.$refs.selectLeft.scrollTop = i.offsetTop;
                }
            }
            if (name == 'GPS') {
                this.$refs.selectLeft.scrollTop = 0;
            }
            if (name == '热门') {
                this.$refs.selectLeft.scrollTop = document.querySelector('.hot-title').offsetTop;
            }
            if (name == '全国') {
                this.$refs.selectLeft.scrollTop = document.querySelector('.all-title').offsetTop;
            }
        },
        tapProvince(province) {
            this.provinceName = province.Name;
            if (province.Childs.length) {
                event.preventDefault();
                event.stopPropagation();
                this.childList = province.Childs;
                this.childAnimate = 'childRightIn';
                return;
            }
            this.$emit('changeCity', { name: province.Name, id: province.Id });
        },
        closeChild() {
            event.preventDefault();
            event.stopPropagation();
            this.childAnimate = 'childRightOut';
        },
        tapCitys(name, id) {
            this.$emit('changeCity', { name: name, id: id })
        },
        onCity(name) {
            this.$emit('changeCity', { name: name, id: '' });
        },
        refresh() {
            event.preventDefault();
            event.stopPropagation();
            this.refreshIng = true;
            return api.search.areas()
                .then(q => {
                    if (q.data.Success) {
                        this.hotCitys = q.data.Content.HotCity;
                        setTimeout(() => {
                            this.refreshIng = false;
                            Toast('定位已刷新');
                        }, 500)
                    }
                })
                .catch(() => {
                    this.refreshIng = false;
                    Toast('获取定位失败');
                })
        }
    }
}
</script>




