'use strict';
import http from '../http';

const prod = {
    getCarts() {//获取购物车
        return http.get(`JS/v7/ShoppingCart/MyShopCart`);
    },
    deleteCart(id) {//删除商品
        return http.get(`JS/v7/ShoppingCart/Del?id=${id}`);
    },
    deleteShelfGoods(){//删除下架商品
        return http.get(`JS/v7/ShoppingCart/ReMoveOffline`);
    },
    getDetails(id) {//获取商品详情
        return http.get(`/js/v7/product/detail?id=${id}`);
    },
    getCard(id) {//获取商品对应卡券
        return http.get(`/js/v7/Coupon/GetHospitalCouponsByHospitalId?id=${id}`);
    },
    postCarts(model) {//加入购物车
        return http.post(`/JS/v7/ShoppingCart/Add`, model);
    },
    postCard(model) {//商品详情领取医院卡券
        return http.post(`/js/v7/Coupon/ReceiveCoupon`, model);
    },
    getComment(id) {//获取商品对应评论
        return http.get(`/js/v7/Product/GetProductAppraise?productid=${id}`);
    },
    getDiary(filters) {
        return http.get(`/js/v7/product/GetProductDiary`, { params: filters });
    },
    postConfirm(model) {//确认订单
        return http.post(`js/v7/EcOrder/ConfirmOrder`,model);
    },
    generateOrder(model){//生成订单
        return http.post(`js/v7/EcOrder/CreateOrder`,model);
    },
    payMoney(model){//支付
        return http.post(`js/v7/EcOrder/GetPaymentMessage`,model);
    },
    paySuccess(id){//支付成功后获取订单信息
        return http.get(`js/v7/EcOrder/GetOrderMessage?paymentId=${id}`);
    },
    myCoupon(model){//获取我的优惠券
        return http.post(`/js/v7/Coupon/SelectCoupon`,model);
    },
    preSetOrder(model) {
        return new Promise(function (resolve, reject) {

            if (!model || !(model instanceof Array)) return reject();

            model = JSON.stringify(model);

            localStorage.setItem("pre-orders", model);
            resolve();
        });

    },
    preGetOrder() {
        return new Promise(function (resolve, reject) {
  
            var json = localStorage.getItem("pre-orders")
            // localStorage.removeItem("pre-orders");
            if (!json) return reject();
            resolve(JSON.parse(json));
           
        });
    },
    changeNumber(model){//更改购物车数量
        return http.post(`JS/v7/ShoppingCart/ModNumber`,model);
    },
    saleTag(id){
        return http.get(`/js/v7/productsaletag/detail?id=${id}`)
    }
};
export default prod;


