<template>
    <div class="m-wrap">
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
    </div>
</template>
  
<style lang="less">
@import url("./style/default.css");
@import url("./less/body.less");
@import url("./font/iconfont.css");
@import url("./less/font.less");
.m-wrap {
  position: relative;
}
.m-header {
  margin-top: env(safe-area-inset-top);
}
[v-cloak] {
  display: none;
}
.m-header-title {
  font-size: @font-34;
}
.defautBg {
  //   width: 100%;height: 100%;
  //   background-image: url(images/def-bg.jpg);
  //   background-size:100% 100%;
}
body {
  padding-top: env(safe-area-inset-top);
}
</style>
<script>
export default {
    mounted() {
        if (!window.cordova) return;
        if (navigator.userAgent.indexOf('Android') > -1) {
            if (!chcp) return;
            chcp.fetchUpdate(this.fetchUpdateCallback);
        }
    },
    methods: {
        installationCallback(error) {
            if (error) {
                console.log('Failed to install the update with error code: ' + error.code);
                console.log(error.description);
            } else {
                console.log('Update installed!');
            }
        },
        fetchUpdateCallback(error, data) {
            if (error) {
                console.log('Failed to load the update with error code: ' + error.code);
                console.log(error.description);
                return;
            }
            console.log('Update is loaded, running the installation');
            chcp.installUpdate(this.installationCallback);
        }
    }
}
</script>



