                            </m-grid-item>
                            <m-grid-item :col="4">
                                item4
                            </m-grid-item>
                            <m-grid-item :col="2">
                                item2
                            </m-grid-item>
                            <m-grid-item :col="5">
                                item5
                            </m-grid-item>
                        </m-grid>
                        <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                            <m-swipe-item class="slide1">
                                <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                            </m-swipe-item>
                            <m-swipe-item class="slide2">
                                <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                            </m-swipe-item>
                            <m-swipe-item class="slide3">
                                <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                            </m-swipe-item>
                        </m-swipe>
                        <m-tabs>
                            <m-tabs class="m-tab" :value="active" @change="handleTabChange">
                                <m-tab value="tab1" title="item1"></m-tab>
                                <m-tab value="tab2" title="item2"></m-tab>
                                <m-tab value="tab3" title="item3"></m-tab>
                            </m-tabs>
                            <m-tab-container :value="active" v-model="active">
                                <m-tab-content value="tab1" title="item1">
                                    <h2>Tab One</h2>
                                    <p>
                                        这是第一个 tab
                                    </p>
                                </m-tab-content>
                                <m-tab-content value="tab2" title="item2">
                                    <h2>Tab Two</h2>
                                    <p>
                                        这是第二个 tab
                                    </p>
                                </m-tab-content>
                                <m-tab-content value="tab3" title="item3">
                                    <h2>Tab Three</h2>
                                    <p>
                                        这是第三个 tab
                                    </p>
                                </m-tab-content>
                            </m-tab-container>
                        </m-tabs>
                    </div>
                </transition>
                <transition name="page2">
                <div v-if="page==2">
                    <div>这里是第二个页面</div>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                    <m-swipe :auto="0" :defaultIndex="1" :show-indicators="true" :continuous="true">
                        <m-swipe-item class="slide1">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0958405777-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide2">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0939335995-banner" />
                        </m-swipe-item>
                        <m-swipe-item class="slide3">
                            <img src="https://cdn-ssl.meb.com/2017/07/28/0948207403-banner" />
                        </m-swipe-item>
                    </m-swipe>
                </div>
               </transition> 
               <div slot="infinite-spinner">{{infiniteText}}</div>
            </m-scroller>
            <actionsheet v-model="showActionsheet" :propsShow="propsShow" ref="actionsheet" :showCancel="true" :cancelText="'取消'" @onChange="onChange">
                <m-action-item @checked="checked" value="测试1">测试1</m-action-item>
                <div class="line"></div>
                <m-action-item @checked="checked" value="测试2">测试2</m-action-item>
            </actionsheet>
            <actionsheet v-model="isShare" :propsShow="propsShow" ref="actionsheets" :showCancel="true" :cancelText="'取消'" @onChange="onChange">
                <m-action-item @checked="checked" value="qq">分享到QQ</m-action-item>
                <div class="line"></div>
                <m-action-item @checked="checked" value="微信">分享到微信</m-action-item>
            </actionsheet>
        </m-center>
    
    </m-container>
</template>
<script>
import { Actionsheet, mActionItem } from 'components/actionsheet';
import { Swipe, SwipeItem } from 'components/swipe';
import { Tab, Tabs, TabContents, TabContent } from 'components/tab';
import Switch from 'components/switch';
import { Swipeout, SwipeoutItem, SwipeoutButton } from 'components/swipeout'
export default {
    data() {
        return {
            title: '列表',
            showActionsheet: false,
            isShare: false,
            selectValue: "",
            propsShow: false,
            topStatus: '',
            loading: false,
            value4: true,
            active: 'tab1',
            viewTop: 0,
            value1: true,
            page: 1,
            infiniteText:'上拉去第二页面',
            loadText:'spinner',
        }
    },
    components: {
        'actionsheet': Actionsheet,
        'm-action-item': mActionItem,
        'm-swipe': Swipe,
        'm-swipe-item': SwipeItem,
        'm-tabs': Tabs,
        'm-tab': Tab,
        'm-tab-container': TabContents,
        'm-tab-content': TabContent,
        'm-switch': Switch,
        'm-swipeout': Swipeout,
        'm-swipeoutItem': SwipeoutItem,
        'm-swipeoutButton': SwipeoutButton,
    },
    created() {
    },
    mounted() {
       console.log()
    },
    methods: {
        loadTop() {
            setTimeout(one => {
                this.$refs.loadmore.onTopLoaded();
            }, 1500)
        },
        infinite() {
            Toast('触发加载更多');
        },
        handleTopChange(status) {
            this.topStatus = status;
        },
        actionsheetChange() {
            this.showActionsheet = true;
        },
        share() {
            this.isShare = true;
        },
        alert() {
            alert(11);
        },
        checked(menu) {
            this.propsShow = false;
            this.selectValue = menu;
            this.$refs.actionsheet.propsShow = false;
            this.$refs.actionsheets.propsShow = false;
        },
        onChange(value) {
            this.selectValue = value;
        },
        handleChange(event) {
            console.log(event);
        },
        handleSwitch(event) {
            console.log(event);
        },
        handleTabChange(val) {
            this.active = val;
        }, handleTabChange(val) {
            this.active = val;
        },
        refresh() {
            console.log('刷新')
        },
        toal(e) {
            this.viewTop = e.y;
        },
        handleEvents() {

        },
        onButtonClick(type) {
            alert(type)
        },
        onReset(done) {
            if (this.page == 2) {
                this.page = 1;
                setTimeout(one => {
                    this.$refs.scroller.refresh();
                    let bottom = this.$refs.scroller.iscroll.maxScrollY;
                    this.$refs.scroller.scrollTo(0, bottom, 0);
                    this.infiniteText = '上拉去第二页';
                    this.loadText = 'spinner';
                    done();
                },100)
             
            }else if(this.page == 1){
                 done();
            }
        },
        onMore(done) {
            if(this.page == 1){
                this.page = 2;
                this.$refs.scroller.refresh();
                this.$refs.scroller.scrollTo(0, 0, 0);
                this.infiniteText = '没有更多数据啦';
                this.loadText = 'resetText';
                done();
            }else if(this.page == 2){
                done();
            }

        },
    },
    watch: {

    }
};
</script>
<style lang="less" scoped>
.m-flex {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.line {
    width: 100%;
    height: 1px;
    background-color: #f5f5f5;
}

.m-swipe-desc {
    text-align: center;
    color: #666;
    margin-bottom: 5px;
}

.m-swipe {
    height: 200px;
    color: #fff;
    font-size: 30px;
    text-align: center;
    margin-bottom: 20px;
}

.m-swipe-item {
    line-height: 200px;
}

.slide1 {
    background-color: #0089dc;
    color: #fff;
}

.slide2 {
    background-color: #ffd705;
    color: #000;
}

.slide3 {
    background-color: #ff2d4b;
    color: #fff;
}

.m-tab {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.m-tab-content {
    width: 100%;
    color: #000;
}
</style>





