import http from '../http';

const doctor = {
     get(id){
        return http.get(`/js/v7/doctor/getdoctor?id=${id}`);
     },
     prodList(filters){
        return http.get(`/js/v7/doctor/getdoctorproducts`, { params: filters });
     },
     diaryList(filters){
        return http.get(`/js/v7/doctor/getdoctordiaries`, { params: filters });
     },
     otherList(filters){
        return http.get(`/js/v7/Doctor/GetDoctorByAreaId`, { params: filters });
     },    
     docProject(id){
        return http.get(`/js/v7/doctor/GetDoctorProjects?doctorId=${id}`);
     },    
     diary(filters){
         return http.get(`/js/v7/doctor/getdoctordiaries`, { params: filters })
     }
}
export default doctor;


