'use strict';
import http from '../http';

const hospital = {
    get(id) {
        return http.get(`/js/v7/hospital/gethospital?id=${id}`);
    },
    getOther(filters)
    {
        return http.get(`/Js/v7/Hospital/GetHospitalByAreaId`,{ params: filters });
    }
};

export default hospital;


