'use strict';
import http from '../http';

const subject = {
    get(filters) {
        return http.get(`/js/v7/Special/Detail`,{ params: filters });
    },
    list(filters) {
        return http.get(`/js/v7/Special/DetailProducts`,{ params: filters });
    },
};

export default subject;


