// 用户相关操作 关注用户 点赞用户帖子
import http from '../http';
const user = {
    follow(model) {//关注
        return http.post(`Js/v7/user/clickfollow`, model);
    },
    likes(model){
        return http.post(`Js/v7/user/clicklike`,model);
    }
}
export default user;


