import Vue from 'vue';
import Router from 'vue-router';
import routes from './routes';

Vue.use(Router);

let router = new Router({ linkActiveClass: 'active', routes: routes });

router["_push"] = router["push"];
router["_back"] = router["back"];
router['action'] = true;

let step = 0;

if (window.plugins)
    window.plugins.nativepagetransitions.globalOptions.iosdelay = 70;

router["push"] = (target, ext) => {

    setTimeout(() => {
        router['_push'](target);
    })

    if (window.plugins)
        window.plugins.nativepagetransitions.slide({
            "direction": "left"
        }, (msg) => {
            step += 1;
        });
    else
        step += 1;

    router['action'] = ext === undefined ?
        true :
        ext;
}
var time;
router.back = (ext) => {
    var lazyTime = 500;
    if (time && time + lazyTime >= Date.now()) {
        return;
    }
    time = Date.now();
    setTimeout(() => {
        router['_back']()
    })

    if (window.plugins)
        window.plugins.nativepagetransitions.slide({
            "direction": "right"
        }, (msg) => {
            step -= 1;
        });
    else
        step -= 1;

    //进入跳转步骤小于0，则关闭当前页面回到原生
    if (window.cordova && step <= 0)
        return cordova.exec(function() {}, function() {}, 'nativeJs', 'view', [{
            name: 'close'
        }]);

    router['action'] = ext === undefined ?
        false :
        ext;

}

router
    .beforeHooks
    .unshift((to, from, next) => {
        next();
    });

router.beforeEach((to, from, next) => {
    next();
})

router.afterEach((to, from) => {});

export default router;


