'use strict';
import http from '../http';

const activity = {
    getActivityDetail(id) {
        return http.get(`/Js/v7/Activity/ActivityDetails?id=${id}`);
    },
    getSignDetails(id) {
        return http.get(`/Js/v7/Activity/SignDetails?id=${id}`);
    },
    myList(filters) {
        return http.get(`/Js/v7/Activity/MyActivity`, { params: filters });
    },
    list: (filters) => {
        return http.get(`/js/V7/Activity/GetActivity`, { params: filters })
    },
    sign: (id, phone) => {
        return http.post(`/js/V7/Activity/Sign`, { activityId: id, userPhone: phone })
    },
    complaint: (type, content, objId, name, contact) => {
        return http.post(`/js/V7/Suggest/UserSuggest`, { type: type, content: content, objId: objId, name: name, contact: contact })
    },
    areaList() {
        return http.get(`/JS/v7/area/getlist`)
    },
    projectList() {
        return http.get('/JS/v7/project/getlist');
    },
};
export default activity;


