import http from '../http';
import axios from 'axios';
const qiniu = {
    getToken(){
        return http.get(`http://api.meb.com/app/v5/home/getqiniutoken?${Date.now() + Math.random(99)}`);
    },
    upload(model){
        return axios.post(`https://up.qbox.me`,model,{headers:{'Content-Types':'multipart/form-data'}});
    }
}
export default qiniu;


