import http from '../http';

const rewardrecord = {
    getList(filters) {
        return http.get(`/js/v7/rewardrecord/getlist`, { params: filters });
    },
    getTopList(filters) {
        return http.get(`/js/v7/rewardrecordtop/getlist`, { params: filters });
    },
    getRecordList(filters){
        return http.get(`/js/v7/inviterecord/getlist`, { params: filters });
    }
}
export default rewardrecord;


