const state = {
    orderId:'',
};
const actions = {
    'orderId_update'({ commit, state }, model) {
        commit('ORDER_RESET', model);
    },
};

const mutations = {
    'ORDER_RESET'(state, data) {
        state.orderId = data;
    }
};
const getters = {};

export default {
    state,
    mutations,
    getters,
    actions
}


