import http from '../http';
const wiki = {
    /**
     * 获取商品聚合/百科详情
     * @param {*} id 
     */
    getWikiDetail(filters) {
        //return http.get(`Js/v6/wiki/details?id=${filters.id}&projectId=${filters.projectId}&areaId=${filters.areaId}`);
        return http.get(`Js/v7/Advertisement/ProductAdvertsementDetail?id=${filters.id}`);
    },
    getProduct(filters){
        return http.get(`Js/v7/Advertisement/GetAdvertisementProductsPage`,{params:filters});
    }
}
export default wiki;


