import http from '../http';
const instalment = {
    /**
     * 获取分期列表
     * @param {*}
     */
    getList(id) {
        return http.get(`/Js/v7/xyqb/GetInstalmentsType`);
    },
    /**
     * 花呗分期
     */
    getHb(id)
    {
        return http.get(`/Js/v7/Xyqb/GetHuaBeiFqNum?id=${id}`);
    },
    /**
     *尾款分期
     *param modle
     */
    getRetainage(modle)
    {
        return http.post(`/Js/v7/Xyqb/PayXyqb`,modle);
    }

}
export default instalment;


