/**
    统计访问文件
 */

import router from '../router'
import api from './api'
import auth from './auth';
router.beforeEach((to, from, next) => {
    let filters = {};

    filters.userId = auth.userId;

    if (to.query.id) filters.id = to.query.id;

    switch (to.name) {
        case 'prod.details':
            filters.type = 2;
            break;
        case 'doctor.detail':
            filters.type = 3;
            break;
        case 'hospital.detail':
            filters.type = 4;
            break;
        case 'activity.detail':
            filters.type = 12;
            break;
        case 'advertisement.detail':
            filters.type = 13;
            break;
        case 'wiki.detail':
            filters.type = 19;
            break;  
        case 'custom.detail':
            filters.type = 20;
            break;                           
        default:
            filters.id = void 0;
            filters.type = void 0;
            break;

    }
    next();
    if (!window.cordova) return;
    if (!filters.type) return;
    api.statistics.get(filters).then(q => { })

})



