import http from '../http';
const advertisement = {
    //术后恢复手册
    handbook(){
        return http.get(`/js/v7/Knowledge/GetOperationRecoveryManual`);
    },
    /**
     * 广告详情
     * @param {*} id 
     */
    getDetail(id) {
        return http.get(`js/v7/Advertisement/detail?id=${id}`);
    },
}
export default advertisement;


